import React, { useMemo, useRef, useState } from 'react';
import { styles } from './styles';
import { KeyboardAvoidingView } from 'react-native-keyboard-controller';
import {
  Image,
  Keyboard,
  Platform,
  ScrollView,
  StatusBar,
  TouchableNativeFeedback,
  View,
} from 'react-native';
import { logo_full } from '../../assets/images';
import { Button, Input, SCREEN_WIDTH, Text } from 'tradewize-component';
import { Formik, FormikProps } from 'formik';
import { globalStyles } from '../../global/styles';
import { getLoginSchema } from '../../validators/login';
import {
  EyeIcon,
  EyeIconOff,
  IconFaceId,
  IconFingerprint,
  IconLock,
} from '../../assets/svg';
import { SvgXml } from 'react-native-svg';
import { useSafeAreaInsets } from 'react-native-safe-area-context';
import colors from '../../theme/colors';
import {
  FormLogin,
  handleBiometricAuthentication,
  handleForgotPassword,
  onCLickLogin,
} from '../../helper/login';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../../redux/store/types';
import i18nISO from '@cospired/i18n-iso-languages';
import { showSelectedLanguageModal } from '../../redux/features/app/actions';
import { SCREENS_ENUM } from '../../types/global';
import { NavigationService } from '../../services/NavigationService';
import DeviceInfo from 'react-native-device-info';
import { useAppTranslation } from '../../hooks/useAppTranslation';
import { useLogin } from '../../hooks/useLogin';

const initialValues: FormLogin = {
  username: '',
  password: '',
};

const Login = () => {
  const formRef = useRef<FormikProps<FormLogin>>(null);
  const { t, i18n } = useAppTranslation();
  const [isSecureText, setIsSecureText] = useState(true);
  const { error } = useSelector((state: RootState) => state.auth);
  const realAppVersion = DeviceInfo.getVersion();
  const dispatch = useDispatch();

  const insets = useSafeAreaInsets();

  // eslint-disable-next-line react-hooks/exhaustive-deps
  const validationSchema = useMemo(() => getLoginSchema(), [t]);

  useLogin(formRef as React.RefObject<FormikProps<FormLogin>>);

  return (
    <ScrollView
      bounces={false}
      alwaysBounceVertical={false}
      style={styles.scrollView}
      showsVerticalScrollIndicator={false}
    >
      <View style={[styles.container, { paddingTop: insets.top }]}>
        <StatusBar barStyle="dark-content" backgroundColor={colors.primary} />
        <KeyboardAvoidingView style={styles.keyboardAvoidingView}>
          <TouchableNativeFeedback onPress={() => Keyboard.dismiss()}>
            <View style={styles.keyboardAvoidingView}>
              <Button
                onPress={() => dispatch(showSelectedLanguageModal())}
                size="small"
                variant="ghost"
                style={styles.selectLanguageButton}
              >
                <Text style={styles.buttonText}>
                  {i18nISO
                    .getName(i18n.language, 'en')
                    ?.toUpperCase()
                    .slice(0, 3)}
                </Text>
              </Button>
              <Image
                source={logo_full}
                style={styles.logo}
                resizeMode="contain"
              />

              <Text style={styles.title} variant="h4">
                {t('WELCOME')}
              </Text>

              <View
                style={[styles.formContainer, { paddingBottom: insets.bottom }]}
              >
                <Formik<FormLogin>
                  innerRef={formRef}
                  initialValues={initialValues}
                  validationSchema={validationSchema}
                  onSubmit={onCLickLogin}
                >
                  {({
                    handleChange,
                    handleSubmit,
                    setTouched,
                    values,
                    errors,
                    touched,
                  }) => (
                    <View style={[styles.inputContainer]}>
                      {(['username', 'password'] as (keyof FormLogin)[]).map(
                        field => {
                          return (
                            <View key={field}>
                              <Text style={styles.labelInput}>
                                {field === 'username'
                                  ? t('EMAIL')
                                  : t('PASSWORD')}
                              </Text>

                              <Input
                                key={
                                  field === 'password'
                                    ? `password-${isSecureText}`
                                    : field
                                }
                                maxLength={field === 'username' ? 150 : 50}
                                isError={!!(errors[field] && touched[field])}
                                borderColorActive={colors.mainColorPrimary}
                                placeholderTextColor={colors.grayScale70}
                                style={styles.input}
                                inputContainerStyle={styles.containerInput}
                                placeholder={
                                  field === 'username'
                                    ? t('ENTER_YOUR_EMAIL_ADDRESS')
                                    : t('ENTER_YOUR_PASSWORD')
                                }
                                keyboardType={
                                  field === 'username'
                                    ? 'email-address'
                                    : 'default'
                                }
                                autoComplete="off"
                                textContentType="none"
                                autoCorrect={false}
                                autoCapitalize="none"
                                onChangeText={text => {
                                  handleChange(field)(text.trim());
                                }}
                                onFocus={() => {
                                  setTouched({ ...touched, [field]: false });
                                }}
                                onBlur={() => {
                                  setTouched({ ...touched, [field]: true });
                                }}
                                value={values[field]}
                                secureTextEntry={
                                  field === 'password' ? isSecureText : false
                                }
                                rightIcon={
                                  field === 'password' && (
                                    <Button
                                      variant="ghost"
                                      onPress={() =>
                                        setIsSecureText(!isSecureText)
                                      }
                                    >
                                      <SvgXml
                                        xml={
                                          !isSecureText
                                            ? EyeIcon()
                                            : EyeIconOff()
                                        }
                                        width={SCREEN_WIDTH * 0.05}
                                        height={SCREEN_WIDTH * 0.05}
                                      />
                                    </Button>
                                  )
                                }
                              />
                              {errors[field] && touched[field] && (
                                <Text style={globalStyles.errorText}>
                                  {errors[field]}
                                </Text>
                              )}
                            </View>
                          );
                        },
                      )}
                      {error && (
                        <Text style={globalStyles.errorBackground}>
                          {error}
                        </Text>
                      )}

                      <Button
                        onPress={handleForgotPassword}
                        variant="ghost"
                        style={styles.forgotPasswordContainer}
                      >
                        <SvgXml
                          xml={IconLock()}
                          width={SCREEN_WIDTH * 0.05}
                          height={SCREEN_WIDTH * 0.05}
                        />
                        <Text style={styles.forgotPasswordText}>
                          {t('FORGOT_PASSWORD')}
                        </Text>
                      </Button>

                      <View style={styles.flexRow}>
                        <Button
                          onPress={() => handleSubmit()}
                          variant="primary"
                          style={styles.loginButton}
                        >
                          <Text style={styles.loginButtonText}>
                            {t('LOGIN')}
                          </Text>
                        </Button>

                        <Button
                          style={styles.loginButtonBiometric}
                          variant="ghost"
                          onPress={handleBiometricAuthentication}
                        >
                          <SvgXml
                            xml={
                              Platform.OS === 'android'
                                ? IconFingerprint()
                                : IconFaceId()
                            }
                            width={SCREEN_WIDTH * 0.1}
                            height={SCREEN_WIDTH * 0.1}
                          />
                        </Button>
                      </View>

                      <Button
                        onPress={() =>
                          NavigationService.navigate(
                            SCREENS_ENUM.RegisterNavigator,
                          )
                        }
                        variant="secondary"
                        style={styles.registerButton}
                      >
                        <Text style={styles.registerButtonText}>
                          {t('DONT_HAVE_AN_ACCOUNT')}{' '}
                          <Text style={styles.registerButtonTextLink}>
                            {t('SIGN_UP')}
                          </Text>
                        </Text>
                      </Button>

                      <Button
                        onPress={() =>
                          NavigationService.navigate(
                            SCREENS_ENUM.HomeGuestScreen,
                          )
                        }
                        variant="primary"
                        style={styles.loginButtonAsGuest}
                      >
                        <Text style={styles.loginButtonTextAsGuest}>
                          {t('CONTINUE_AS_A_GUEST')}
                        </Text>
                      </Button>

                      <Text style={styles.versionText}>
                        {t('VERSION', { version: realAppVersion })}
                      </Text>

                      <Button
                        variant="ghost"
                        onPress={() => {
                          NavigationService.navigate(
                            SCREENS_ENUM.LegalAndPrivacyPolicyScreen,
                          );
                        }}
                        style={styles.btnPolicyContainer}
                      >
                        <Text style={styles.txtPolicy}>
                          {t('LEGAL_PRIVACY_POLICY')}
                        </Text>
                      </Button>
                    </View>
                  )}
                </Formik>
              </View>
            </View>
          </TouchableNativeFeedback>
        </KeyboardAvoidingView>
      </View>
    </ScrollView>
  );
};

export default Login;
