import { requestPermissionBiometric } from '.';
import i18n from '../i18n';
import {
  hideAlert,
  isShowSuggestSettingLoginBiometricModal,
  showAlert,
} from '../redux/features/app/actions';
import {
  loginAsync,
  loginBiometricAsync,
} from '../redux/features/auth/actions';
import { store } from '../redux/store';
import { NavigationService } from '../services/NavigationService';
import { STORAGE_KEY, StorageService } from '../services/StorageService';
import { SCREENS_ENUM } from '../types/global';
import { logger } from '../utils';
import ReactNativeBiometrics from 'react-native-biometrics';

const rnBiometrics = new ReactNativeBiometrics();

export interface FormLogin {
  username: string;
  password: string;
}
export const onCLickLogin = async (values: FormLogin) => {
  try {
    const { username, password } = values;
    if (!username || !password) {
      return;
    }
    const infoBiometricSaved = await StorageService.get(
      STORAGE_KEY.BIOMETRIC_KEY,
    );
    const useLoginSaved = await StorageService.get(STORAGE_KEY.USER_LOGIN);
    if (
      (useLoginSaved && useLoginSaved !== username) ||
      !infoBiometricSaved?.biometricKey
    ) {
      store.dispatch(isShowSuggestSettingLoginBiometricModal(true));
    }

    StorageService.set(STORAGE_KEY.USER_LOGIN, username);
    store.dispatch(loginAsync({ username, password }));
  } catch (error) {
    logger('error onCLickLogin', error);
  }
};

export const handleForgotPassword = () => {
  NavigationService.navigate(SCREENS_ENUM.ForgotPasswordScreen);
};

export const handleBiometricAuthentication = async () => {
  try {
    const isAvailable = await rnBiometrics.isSensorAvailable();
    if (!isAvailable?.available) {
      store.dispatch(
        showAlert({
          visible: true,
          type: 'warning',
          title: i18n.t('WARNING'),
          message: i18n.t(
            'YOUR_DEVICE_IS_NOT_SUPPORTED_OR_YOU_HAVE_NOT_REGISTERED_BIOMETRICS_ON_YOUR_DEVICE',
          ),
          buttonText: i18n.t('CONFIRM'),
          onClose: () => {
            store.dispatch(hideAlert());
          },
        }),
      );
      return;
    }

    const biometricSaved = await StorageService.get(STORAGE_KEY.BIOMETRIC_KEY);
    if (biometricSaved?.biometricKey) {
      const permission = await requestPermissionBiometric();
      if (permission?.granted) {
        store.dispatch(loginBiometricAsync());
      }
    } else {
      store.dispatch(
        showAlert({
          visible: true,
          type: 'warning',
          title: i18n.t('WARNING'),
          message: i18n.t(
            'YOUR_DEVICE_IS_NOT_SUPPORTED_OR_YOU_HAVE_NOT_REGISTERED_BIOMETRICS_ON_YOUR_DEVICE',
          ),
          buttonText: i18n.t('CONFIRM'),
          onClose: () => {
            store.dispatch(hideAlert());
          },
        }),
      );
    }
  } catch (error) {
    logger('error handleBiometricAuthentication', error);
  }
};
