import i18n from '../i18n';
import * as Yup from 'yup';

export const getLoginSchema = () =>
  Yup.object({
    username: Yup.string()
      .min(3, i18n.t('USERNAME_MUST_BE_BETWEEN_3_AND_150_CHARACTERS'))
      .max(150, i18n.t('USERNAME_MUST_BE_BETWEEN_3_AND_150_CHARACTERS'))
      .required(i18n.t('USERNAME_CANNOT_BE_EMPTY')),
    password: Yup.string()
      .min(
        8,
        i18n.t('PASSWORD_MUST_BE_AT_LEAST_NUMBER_CHARACTERS_LONG', {
          number: 8,
        }),
      )
      .required(i18n.t('PASSWORD_CANNOT_BE_EMPTY')),
  });
