import { useFocusEffect } from '@react-navigation/native';
import { useCallback, useEffect } from 'react';
import { Platform, StatusBar } from 'react-native';
import colors from '../theme/colors';
import { FormLogin, handleBiometricAuthentication } from '../helper/login';
import { RootState } from '../redux/store/types';
import { useSelector } from 'react-redux';
import { logger } from '../utils';
import { STORAGE_KEY, StorageService } from '../services/StorageService';
import { FormikProps } from 'formik';

export const useLogin = (formRef: React.RefObject<FormikProps<FormLogin>>) => {
  const isLoginBiometricAuthentication = useSelector(
    (state: RootState) => state.auth.isLoginBiometricAuthentication,
  );
  const language = useSelector((state: RootState) => state.app.language);

  const checkSettingBio = useCallback(async () => {
    try {
      const biometricSaved = await StorageService.get(
        STORAGE_KEY.BIOMETRIC_KEY,
      );

      if (biometricSaved?.biometricKey && isLoginBiometricAuthentication) {
        handleBiometricAuthentication();
      }
    } catch (error) {
      logger('error checkSettingBio', error);
    }
  }, [isLoginBiometricAuthentication]);

  const fillUserNameSaved = useCallback(async () => {
    const userLogin = await StorageService.get(STORAGE_KEY.USER_LOGIN);
    if (userLogin) {
      formRef.current?.setFieldValue('username', userLogin);
    }
  }, [formRef]);

  useEffect(() => {
    fillUserNameSaved();
  }, [fillUserNameSaved]);

  useEffect(() => {
    formRef?.current?.validateForm();
  }, [formRef, language]);

  useFocusEffect(
    useCallback(() => {
      checkSettingBio();
      setTimeout(() => {
        StatusBar.setBarStyle('dark-content');
        if (Platform.OS === 'android') {
          StatusBar.setBackgroundColor(colors.primary);
        }
      }, 300);
    }, [checkSettingBio]),
  );
};
